.footer{
    background-color: #0c0c0c;
    .inner-footer{
        .footer-top{
            padding: 75px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.144);
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            .ul-item{
                width: 25%;
                h3{
                    font-size: 15px;
                    margin-bottom: 14px;
                    color: #f0f0f0;
                }
                li{
                    margin: 5px 0;
                    display: block;
                    a{
                        color: #888;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 1.95;
                    }
                }
            }
            .footer-logo{
                width: 25%;
                img{
                    width: 90px;
                    height: 70px;
                }
                p{
                    color: #888;
                        font-size: 14px;
                        font-weight: 500;
                        line-height: 1.95;
                    &.pocket{
                        text-align: right;
                    }    
                }
            }
        }

        .social{
            padding: 15px 0;
            display: flex;
            justify-content: space-between;
            p{
                color: #c7c7c7;
                font-size: 14px;
                font-weight: 400;
                line-height: 1.95;
                padding-bottom: 0;
            }

            .social-icons{
                display: flex;
                li{
                    margin-left: 8px;
                    a{
                        img{
                            width: 28px;
                            height: 28px;
                            object-fit: contain;
                        }
                    }
                }
            }
        }
    }
}