@media screen and (max-width: 600px) {
    .footer{
        .inner-footer{
            .footer-top{
                flex-wrap: wrap;
                .ul-item{
                    width: 50%;
                    margin-bottom: 20px;
                }

                .footer-logo{
                    width: 50%;
                }
            }

            .social{
                justify-content: center;
                flex-direction: column;
                align-items: center;
                P{
                    margin-bottom: 20px;
                }
            }
        }
    }
}

@media screen and (max-width: 400px) {
    .footer{
        .inner-footer{
            .footer-top{
                padding: 30px 0;
                flex-wrap: wrap;
                align-items: center;
                .ul-item{
                    width: 100%;
                    margin-bottom: 20px;
                    text-align: center;
                }

                .footer-logo{
                    width: 100%;
                    text-align: center;
                }
            }

            .social{
                justify-content: center;
                flex-direction: column;
                align-items: center;
                P{
                    margin-bottom: 20px;
                }
            }
        }
    }
}
