@media screen and (max-width: 992px) {
    .main{
        .info-me{
            &_inner{
                display: flex;
                flex-direction: column;
            }
        }
        .serv-row{
            justify-content: space-around;
        }

        .get-in-touch{
            .inner-get{
                .form-wrapper{
                    .left{
                        .input-one{
                            width: 100%;
                        }
                        form{
                            input{
                                width: 100%;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1024px) {
    .main{
        .serv-row{
            justify-content: space-around;
        }
    }
}

@media screen and (max-width:768px) {
    .main{
        .info-me{
            &_inner{
                flex-direction: column;
            }
        }
        .serv-row{
            .serv-items{
                padding: 14px 17px;
            }
        }

        .my-resume{
            .inner-resume{
                .my-degree{
                    justify-content: space-around;
                }
            }
        }

        .my-skills{
            .inner-skills{
                .skils{
                    flex-wrap: wrap;
                    justify-content: space-between;
                    .skils-items{
                        width: 50%;
                        margin-bottom: 30px;
                    }
                }
            }
        }

        .my-blog{
            .blogs-wrapper{
                flex-wrap: wrap;
                justify-content: center;
                .blogs{
                    margin-bottom: 30px;
                    max-width: 100%;
                    h2{
                        font-size: 30px;
                    }
                    p{
                        font-size: 16px;
                    }

                    img{
                        width: 100%;
                    }
                }
            }
        }
    }
}

@media screen and (max-width:600px) {
    .main{
        .my-skills{
            .inner-skills{
                .other-skil{
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: flex-start;
                    .skil-item{
                        margin-bottom: 20px;
                    }
                }
            }
        }
       .info-me{
           &_inner{
               .left{
                display: flex;
                justify-content: center;
                   .info-img{
                       width: 300px;
                       
                   }
               }
           }
       }

       .serv-row{
           .serv-items{
            padding: 14px 17px;
            width: 100%;
           }
       }

       .work-status{
           .inner-work{
               align-items: flex-start;
               flex-direction: column;
               .title{
                   margin-bottom: 20px;
               }
           }
       }

       .get-in-touch{
        .inner-get{
            .form-wrapper{
                flex-direction: column;
                display: flex;
                .left{
                    width: 100%;
                    margin-bottom: 30px;
                }
                .right{
                    width: 100%;
                    padding: 0;
                }
            }
        }
    }
    }
}

@media screen and (max-width: 425px) {
    .main{
        .info-me{
            padding: 40px 0;
            &_inner{
                .left{
                    padding: 0;
                    .info-img{
                        width: 200px;
                    }
                }
               .right{
                    .full-info{
                        flex-direction: column;
                        align-items: flex-start;
                        .conect{
                            padding-left: 0;
                        }
                    }
                    

                    .link{
                        flex-direction: column;
                        a{
                            margin-bottom: 10px;
                            &.white{
                                margin: 0;
                            }
                        }
                    }
                }
            }
        }
        .serv-row{
            .serv-items{
                width: 100%;
            }
        }

        .my-skills{
            .inner-skills{

                .skils{
                    flex-wrap: wrap;
                    justify-content: center;
                    margin: 0 auto;
                    text-align: center;
                    .skils-items{
                        width: 100%;
                    }
                }
                .other-skil{
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: flex-start;
                    .skil-item{
                        margin-bottom: 20px;
                        width: 100%;
                    }
                }
            }
        }

        .get-in-touch{
            .inner-get{
                .form-wrapper{
                    flex-direction: column;
                    display: flex;
                    .left{
                        width: 100%;
                    }
                    .right{
                        width: 100%;
                        padding: 0;
                    }
                }
            }
        }

        .my-service{
            padding: 40px 0;
        }
        .my-resume{
            padding: 40px 0;
        }
    }
}


