.single{
    display: flex;
    justify-content: space-around;
    z-index: 100;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    .single-line{
       background-color: rgba($color: rgb(37, 37, 37), $alpha: 0.4);
       width: 2px;
       min-height: 100%;
       display: block;
    }
}



.main{
    position: relative;
}

.main{
    .info-me{
        background-color: rgba($color: #0f0f0f, $alpha: 0.8);
        padding: 105px 0;
        &_inner{
            display: flex;
            .left{
                padding: .50rem;
                .info-img{
                    width: 430px;
                    border: 1px solid #afafaf57;
                    border-radius: .25rem;
                    height: auto;
                    padding: .25rem;
                }   
            }

            .right{
                padding-left: .50rem;
                .name{
                    font-size: 42px;
                    font-weight: 700;
                    margin-bottom: 11px;
                    color: #f0f0f0;
                }
                .about-direc{
                    font-size: 20px;
                    margin-bottom: 22px;
                    color: #4470d9;
                }

                .some-title{
                    margin-bottom: 1rem;
                    margin-top: 0;
                    font-size: 15px;
                    font-weight: 400;
                    line-height: 1.95;
                    color: #9f9f9f;
                    padding-bottom: 33px;
                    position: relative;
                    border-bottom: 2px solid rgba(255, 255, 255, 0.062);
                }

                .full-info{
                    padding: 33px 0;
                    display: flex;
                    align-items: center;
                    .full-name{
                        h2{
                            list-style: normal;
                            line-height: inherit;
                            font-weight: 600;
                            color: #d9d9d9;
                            font-size: 15px;
                            margin-bottom: 10px;
                            span{
                                opacity: 0.5;
                                padding-left: 10px;
                            }
                        }
                    }

                    .conect{
                        padding-left: 100px;
                        h2{
                            list-style: normal;
                            line-height: inherit;
                            font-weight: 600;
                            color: #d9d9d9;
                            font-size: 15px;
                            margin-bottom: 10px;
                            span{
                                padding-left: 10px;
                                opacity: 0.5;

                            }
                            a{
                                color: #4770d9;
                                padding-left: 10px;
                                opacity: 0.8;
                            }
                        }
                    }
                }
                .link{
                    display: flex;
                    align-items: center;
                    a{
                        border-radius: 30px;
                        font-size: 14px;
                        padding: 8px 42px;
                        font-weight: 600;
                        &.white{
                            background-color: #fff;
                            color: #4470d9;
                            margin-left: 20px;
                        }
                        &.blue{
                            color: #fff;
                            background-color: #4470d9;
                        }
                    }
                }
            }
        }
    }

    .my-service{
        padding: 103px 0;

        .service-name{
            display: inline-block;
            font-size: 40px;
            font-weight: 800;
            letter-spacing: -.25px;
            line-height: 100%;
            margin-bottom: 12px;
            position: relative;
            z-index: 9;
            color: #fff;
            position: relative;
            &::before{
                position: absolute;
                content: '';
                bottom: 0;
                display: block;
                height: 52%;
                width: 85%;
                z-index: -9;
                background-color: #3663d6;
            }
        }
        .serv-title{
            margin-bottom: 6rem;
            font-size: 15px;
            font-weight: 400;
            line-height: 1.95;
            color: #9f9f9f;
        }
    }

    .serv-row{
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        .serv-items{
            padding: 27px 80px 27px 24px;
            margin-bottom: 37px;
            display: flex;
            align-items: flex-start;
            background-color: #0c0c0c;
            .serv-icon{
                width: 50px;
                height: 50px;
                object-fit: contain;
                margin-right: 22px;
            }
            h2{
                font-size: 18px;
                font-weight: 700;
                letter-spacing: -.5px;
                margin-bottom: 8px;
                color: #dfdfdf;
            }
            p{
                font-weight: 400;
                line-height: 1.95;
                font-size: 13px;
                color: #9f9f9f;
            }
        }
    }

    .work-status{
        padding: 80px 0;
        background-attachment: fixed;
        background-position: 50% 0;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        &::before{
            content: '';
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            width: 100%;
            background-color: rgba(0,0,0,.9);
        }
        .inner-work{
            display: flex;
            z-index: 100;
            align-items:  center;
            justify-content: space-between;
            .title{
                z-index: 1;
                h1{
                    font-weight: 700;
                    margin-bottom: 14px;
                    color: #f0f0f0;
                    font-size: 2.5rem;
                    line-height: 1.2;
                }
                p{
                    font-size: 15px;
                    margin-bottom: 0;
                    font-weight: 400;
                    line-height: 1.95;
                    color: #9f9f9f;
                }
            }
            .hire-me{
                border-radius: 30px;
                font-size: 14px;
                padding: 8px 42px;
                background-color: #4770d9;
                color: #fff;
                z-index: 1;
            }
        }
    }

    .my-resume{
        padding: 83px 0;
        background-color: #00000098;
        .inner-resume{
            h1{
                display: inline-block;
                font-size: 40px;
                font-weight: 800;
                letter-spacing: -.25px;
                line-height: 100%;
                margin-bottom: 12px;
                position: relative;
                z-index: 9;
                color: #fff;
                position: relative;
                &::before{
                    position: absolute;
                    content: '';
                    bottom: 0;
                    display: block;
                    height: 52%;
                    width: 85%;
                    z-index: -9;
                    background-color: #3663d6;
                }
            }
            p{
                margin-bottom: 6rem;
                font-size: 15px;
                font-weight: 400;
                line-height: 1.95;
                color: #9f9f9f;
            }
            .my-degree{
                display: flex;
                justify-content: space-between;
                align-items: center;
                flex-wrap: wrap;
               .items
                 .edu-name{
                    font-weight: 400;
                    line-height: 1.95;
                    margin-right: 15px;
                    font-size: 15px;
                    color: #6084de;
                        .year{
                            color: #fff;
                        }
                    }
                .line{
                    display: block;
                    max-width: 100%;
                    height: 6px;
                    background-color: #4770d9;
                    margin: 11px 0;
                    position: relative;
                    &::before{
                        clip-path: polygon(0 10%, 73% 10%, 100% 50%, 73% 85%, 0 85%);

                        position: absolute;
                        content: '';
                        width: 20px;
                        height: 30px;
                        background-color: #fff;
                        transform: rotate(90deg);
                        right: 50%;
                        top: -5px;
                    }
                }

                .degre{
                    font-size: 16px;
                    font-weight: 600;
                    margin-bottom: 6px;
                    margin-top: 33px;
                    line-height: 1.2;
                    color: #f0f0f0;
                }

                .some-ttl{
                    margin-bottom: 6rem;
                    font-size: 15px;
                    font-weight: 400;
                    line-height: 1.95;
                    color: #9f9f9f;
                }
            }
            
        }
    }

     .my-skills{
         padding: 90px 0;
         .inner-skills{
            h1{
                display: inline-block;
                font-size: 40px;
                font-weight: 800;
                letter-spacing: -.25px;
                line-height: 100%;
                margin-bottom: 12px;
                position: relative;
                z-index: 9;
                color: #fff;
                position: relative;
                &::before{
                    position: absolute;
                    content: '';
                    bottom: 0;
                    display: block;
                    height: 52%;
                    width: 85%;
                    z-index: -9;
                    background-color: #3663d6;
                }
            }
            p{
                padding-bottom: 6rem;
                font-size: 15px;
                font-weight: 400;
                line-height: 1.95;
                color: #9f9f9f;
            }

            .skils{
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                .skils-items{
                    span{
                        color: #9f9f9f;
                        position: relative;
                        &.before{
                            &::before{
                                position: absolute;
                                content: 'learning...';
                                top: -20px;
                                font-size: 10px;
                                color: rgb(0, 255, 21);
                                animation: animate 3s linear infinite;
                            }
                        }
                    }
                    .tech{
                        display: inline-block;
                        font-size: 27px;
                        font-weight: 400;
                        letter-spacing: -.25px;
                        line-height: 100%;
                        margin-bottom: 50px;
                        color: rgb(235, 235, 235);
                    }

                    .img-span{
                        display: flex;
                        margin-bottom: 20px;
                        align-items: flex-end;
                        img{
                            width: 50px;
                            height: 50px;
                            object-fit: contain;
                            padding-right: 30px;
                        }
                    }
                }
            }


            .other-skill{
                margin-top: 40px;
                .other-skil{
                    display: flex;
                    justify-content: space-around;
                    h2{
                        display: inline-block;
                        font-size: 27px;
                        font-weight: 400;
                        letter-spacing: -.25px;
                        line-height: 100%;
                        margin-bottom: 50px;
                        color: rgb(235, 235, 235);
                    }
                    .skil-item{
                        .img-span{
                            display: flex;
                            align-items: flex-end;
                            margin-bottom: 20px;
                            img{
                                width: 50px;
                                height: 50px;
                                object-fit: cover;
                                padding-right: 30px;
                            }
                            span{
                                color: #9f9f9f;
                            }
                        }
                       
                    }
                }
            }
         }
     }


     .testemonials{
         background-color: #0f0f0f94;
         padding: 90px 0;
         .inner-test{
             h1{
                display: flex;
                justify-content: center;
                text-align: center;
                font-size: 40px;
                font-weight: 800;
                letter-spacing: -.25px;
                line-height: 100%;
                margin-bottom: 12px;
                position: relative;
                z-index: 9;
                color: #fff;
                &::before{
                    position: absolute;
                    content: '';
                    bottom: 0;
                    display: block;
                    height: 52%;
                    width: 20%;
                    left: 38%;
                    z-index: -9;
                    background-color: #3663d6;
                }
             }
             p{
                padding-bottom: 6rem;
                font-size: 15px;
                font-weight: 400;
                line-height: 1.95;
                color: #9f9f9f;
                text-align: center;
                &.content1{
                    padding-bottom: 35px;
                }
                &.content{
                    padding-bottom: 35px;
                    max-width: 770px;
                    text-align: center;
                    font-style: italic;
                    margin: 0 auto;
                }
             }

             .avatar-img{
                 width: 50px;
                 height: 50px;
                 object-fit: contain;
                 border-radius: 50%;
                 box-shadow: 0 0 7px 0 rgb(0 0 0 / 33%);
                 padding-right: 11px;
             }

             .avatar-wrap{
                 display: flex;
                 justify-content: center;
                 padding-bottom: 40px;
                 .avatar{
                     display: flex;
                     align-items: center;
                     span{
                         display: block;
                         &.name{
                            font-size: 14px;
                            font-weight: 600;
                            margin-bottom: 0;
                            color: #fff;
                         }
                         &.degre{
                            font-size: 13px;
                            color: #888;
                         }
                     }
                 }
             }

             .quote{
                 width: 59px;
                 height: 67px;
                 margin: 0 auto;
                 padding-bottom: 35px;
            }
         }
     }

