     .get-in-touch{
         padding: 105px 0;
         background-color: #0f0f0f;
         h1{
            display: inline-block;
            font-size: 40px;
            font-weight: 800;
            letter-spacing: -.25px;
            line-height: 100%;
            margin-bottom: 12px;
            position: relative;
            z-index: 9;
            color: #fff;
            position: relative;
            &::before{
                position: absolute;
                content: '';
                bottom: 0;
                display: block;
                height: 52%;
                width: 85%;
                z-index: -9;
                background-color: #3663d6;
            }
         }
         p{
            padding-bottom: 6rem;
            font-size: 15px;
            font-weight: 400;
            line-height: 1.95;
            color: #9f9f9f;
            padding-bottom: 0;
            margin-bottom: 60px;
         }

         

         .form-wrapper{
             display: flex;
             justify-content: space-between;
             align-items: flex-start;
             width: 100%;
             .left{
                h4{
                    font-weight: 600;
                    font-weight: 600;
                    font-size: 22px;
                    color: #f0f0f0;
                    line-height: 1.2;
                    margin-bottom: 28px;
                 }
                 input, textarea{
                     &:focus{
                         border-bottom: 1px solid #4770d9;
                     }
                 }
                 width: 63%;
                 .input-one{
                    background-color: #0b0b0b;
                    border: none;
                    border-radius: 0;
                    border-bottom: 1px solid #1d1d1d;
                    font-size: 14px;
                    height: 41px;
                    padding: 9px 14px;
                    width: 48%;
                    box-sizing: border-box;
                    outline: none;
                    color: #777;
                    margin-bottom: 25px;
                    &.two{
                        margin-right: 20px;
                        width: 49%;
                    }
                 }

                 .input-two{
                     width: 100%;
                     display: block;
                     background-color: #0b0b0b;
                    border: none;
                    border-radius: 0;
                    border-bottom: 1px solid #1d1d1d;
                    font-size: 14px;
                    height: 41px;
                    padding: 9px 14px;
                    box-sizing: border-box;
                    outline: none;
                    color: #777;
                    margin-bottom: 25px;
                 }

                 textarea{
                     width: 100%;
                     display: block;
                     background-color: #0b0b0b;
                    border: none;
                    border-radius: 0;
                    border-bottom: 1px solid #1d1d1d;
                    font-size: 14px;
                    height: 41px;
                    padding: 9px 14px;
                    box-sizing: border-box;
                    outline: none;
                    color: #777;
                    margin-bottom: 25px;
                    height: 122px;
                    resize: vertical;
                 }

                 button{
                    padding: 8px 20px;
                    color: #fff;
                    background-color: #4770d9;
                    cursor: pointer;
                    border-radius: 30px;
                    font-size: 14px;
                    border: none;
                    font-weight: 400;
                    line-height: 1.5;
                 }
             }
             .right{
                 width: 37%;
                 padding: 0 25px;
                 h4{
                    font-weight: 600;
                    font-weight: 600;
                    font-size: 22px;
                    color: #f0f0f0;
                    line-height: 1.2;
                    margin-bottom: 28px;
                 }
                 P{
                    font-size: 14px;
                    margin-bottom: 28px;
                    color: #9f9f9f;
                    font-weight: 400;
                    line-height: 1.95;
                 }

                 ul{
                     position: relative;
                     &::before{
                        position: absolute;
                        content: '';
                        height: 100%;
                        width: 2px;
                        background-color: rgba($color: #fff, $alpha: 0.15);
                        left: 45px;
                     }
                     li{
                         margin-bottom: 26px;
                         .items{
                             display: flex;
                             align-items: center;
                             img{
                                 width: 27px;
                                 height: 36px;
                                 padding-right: 40px;
                             }
                             .names{
                                 h6{
                                    font-size: 14px;
                                    font-weight: 600;
                                    margin-bottom: 0;
                                    color: #f0f0f0;
                                    line-height: 1.2;
                                 }
                                 span{
                                    font-size: 14px;
                                    font-weight: 400;
                                    line-height: 1.95;
                                    color: #888;
                                 }
                                 a{
                                    color: #4770d9;
                                    font-size: 14px;
                                    font-weight: 400;
                                    line-height: 1.95;
                                 }
                             }
                         }
                     }
                 }
             }
         }
     }
}



// slick-slider

.slick-next, .slick-prev{
    display: none !important;
}
.slick-dots{
    li{
        width: 10px !important;
        height: 10px !important;
        &.slick-active{
            button{
                background-color: #867791 !important;
            }
        }
        button{
            background-color: #ffffff !important;
            width: 8px !important;
            height: 8px !important;
            border-radius: 50% !important;
            transition: 0.1s;
            &:hover{
                background-color: #867791 !important;
            }
            &::before{
                display: none !important;
            }
        }
    }
}
